import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Categoria } from 'models/categoria.model';
import { Subscription } from 'rxjs';
import { InserimentoqueryService } from '../inserimentoquery.service';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;




@Component({
  selector: 'app-noleggionewcliente',
  templateUrl: './noleggionewcliente.component.html',
  styleUrls: ['./noleggionewcliente.component.css']
})
export class NoleggionewclienteComponent implements OnInit {
  categoria: Categoria[]= [];
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    ) { }

  ngOnInit(): void {
  }

  flag = false;
  boxingres;
  

  
  retrieveInfo(){
    var id=parseInt((document.getElementById('id') as HTMLInputElement).value)
    var data_inizio=(document.getElementById('datainizio') as HTMLInputElement).value;
    var data_fine=(document.getElementById('datafine') as HTMLInputElement).value;
    var nominativo=(document.getElementById('nominativo') as HTMLInputElement).value;
    var patente=(document.getElementById('patente') as HTMLInputElement).value;
    var cc=(document.getElementById('cc') as HTMLInputElement).value;

    if(this.query.addCliente(id,nominativo,patente,cc)){
      console.log(this.boxingres[0].id);
      let flagunlock=true;
      if(flagunlock){
          if(this.query.noleggio(id,this.boxingres[0].id,data_inizio,data_fine)){
            this.flag=true
            alert("Noleggio effettuato con successo!");
            window.location.reload();
            return this.flag;
          }
          else{
            alert("Errore Noleggio!")
            return;
          }
      }
      else{
        alert("Errore Targa!")
        return;
      }
    }else{
      alert("Errore inserimento Cliente!")
      return;
    }
  }
  unlockflag2=false;
  getAuto(){
    
    var targa=(document.getElementById('targa') as HTMLInputElement).value;
    const categoriaDaCercare = {targa : targa};
      this.http
      .post<{message: string, risposta: any}>('http://localhost:3000/api/categoria/id_auto', categoriaDaCercare)
      .subscribe(
      (responseData)=>{
        console.log(responseData.message);
        this.boxingres=responseData.risposta;
      }
      )
      return this.unlockflag2=true;
  }

  generatePdf(){
    const documentDefinition = document.getElementById('exampleModal') as HTMLInputElement;
    pdfMake.createPdf(documentDefinition).download();
    alert("Download in corso");
    window.location.reload();
   }





}
