import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Categoria } from 'models/categoria.model';
import { Subscription } from 'rxjs';
import { InserimentoqueryService } from '../inserimentoquery.service';

@Component({
  selector: 'app-visualizza',
  templateUrl: './visualizza.component.html',
  styleUrls: ['./visualizza.component.css']
})
export class VisualizzaComponent implements OnInit, OnDestroy {
  descmod;
  boolflag=false;
  categoria: Categoria[]= [];
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    
    ) { }

  ngOnInit(): void {
  }

  ngOnDestroy() {
    
  }

  cercareAuto(){
    var desc2=(document.getElementById('descrizioneDaCercare') as HTMLInputElement).value;

    if(this.query.cercaAuto(desc2)){
      this.categoriaSub = this.query.getCategoriaListener()
      .subscribe((categoria: any) => {
        this.categoria = categoria;
    });
    }
    return this.descmod=desc2;
  }
  
  svuotaCache(){
    window.location.reload();
  }
  


}
