import { Component, OnDestroy, OnInit } from '@angular/core';
import { InserimentoqueryService } from '../inserimentoquery.service';
import { HttpClient } from '@angular/common/http';
import { Categoria } from 'models/categoria.model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-modifica',
  templateUrl: './modifica.component.html',
  styleUrls: ['./modifica.component.css']
})
export class ModificaComponent implements OnInit, OnDestroy {
  descmod;
  boolflag=false;
  categoria: Categoria[]= [];
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    ) { }

  ngOnInit(): void {
  }

  ngOnDestroy() {
  }

  cercareInfo(){
    var desc2=(document.getElementById('descrizioneDaCercare') as HTMLInputElement).value;

    if(this.query.cercaCategoria(desc2)){
      this.categoriaSub = this.query.getCategoriaListener()
      .subscribe((categoria: Categoria[]) => {
        this.categoria = categoria;
    });
    }
    return this.descmod=desc2;
  }

  flag(){
    return this.boolflag=true;
  }

  retrieveInfo(){
    var desc=(document.getElementById('descrizione') as HTMLInputElement).value;
    var prezzoGiornaliero=(document.getElementById('prezzogiornaliero') as HTMLInputElement).value;
    var prezzoSettimana=(document.getElementById('prezzosettimanale') as HTMLInputElement).value;
    var prezzoMensile=(document.getElementById('prezzomensile') as HTMLInputElement).value;

    if(this.query.modificaCategoria(desc,parseFloat(prezzoGiornaliero),parseFloat(prezzoSettimana),parseFloat(prezzoMensile), this.descmod)){
      alert("Categoria modificata con successo!")
      window.location.reload();
    }
  }
  svuotaCache(){
    window.location.reload();
  }


}
