import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { TestBed } from '@angular/core/testing';
import { Categoria } from 'models/categoria.model';
import { Subscription } from 'rxjs';
import { InserimentoqueryService } from '../inserimentoquery.service';

@Component({
  selector: 'app-elenco',
  templateUrl: './elenco.component.html',
  styleUrls: ['./elenco.component.css']
})
export class ElencoComponent implements OnInit,OnDestroy {
  categoria: any= [];
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    
    ) { }

  ngOnInit(): void {
    this.start();
  }

  ngDoChek(){
    this.start();
  }

  ngOnDestroy() {
    
  }

  start(){
    this.http.get<{message: string, categoria: any}>('http://localhost:3000/api/categoria').subscribe(
        (categoriaData)=>{
            this.categoria = categoriaData.categoria;
            return this.categoria
        });
  }
}
