import { Component, OnDestroy, OnInit } from '@angular/core';
import { InserimentoqueryService } from '../inserimentoquery.service';
import { HttpClient } from '@angular/common/http';
import { Categoria } from 'models/categoria.model';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-inserisci',
  templateUrl: './inserisci.component.html',
  styleUrls: ['./inserisci.component.css']
})
export class InserisciComponent implements OnInit, OnDestroy {
  categoria: Categoria[]= [];
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    ) { }

  ngOnInit(): void {
    this.query.getCategoria();
    this.categoriaSub = this.query.getCategoriaListener()
      .subscribe((categoria: Categoria[]) => {
        this.categoria = categoria;
    });
  }


  ngOnDestroy() {
    this.categoriaSub.unsubscribe();
  }

  retrieveInfo(){
    var id=(document.getElementById('idint') as HTMLInputElement).value
    var desc=(document.getElementById('descrizione') as HTMLInputElement).value;
    var prezzoGiornaliero=(document.getElementById('prezzogiornaliero') as HTMLInputElement).value;
    var prezzoSettimana=(document.getElementById('prezzosettimanale') as HTMLInputElement).value;
    var prezzoMensile=(document.getElementById('prezzomensile') as HTMLInputElement).value;

    if(this.query.addCategoria(parseInt(id),desc,parseFloat(prezzoGiornaliero),parseFloat(prezzoSettimana),parseFloat(prezzoMensile))){
      alert("Categoria inserita con successo!")
      window.location.reload();
    }
  }

}
