import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Categoria } from 'models/categoria.model';
import { Subscription } from 'rxjs';
import { InserimentoqueryService } from '../inserimentoquery.service';

@Component({
  selector: 'app-cancella',
  templateUrl: './cancella.component.html',
  styleUrls: ['./cancella.component.css']
})
export class CancellaComponent implements OnInit,OnDestroy {
  descmod;
  categoria: Categoria[]= [];
  flag=false;
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    ) { }

  ngOnInit(): void {
  }

  ngOnDestroy() {
  }

  avvisoDiSicurezza(){
    this.flag=true;
    var desc2=(document.getElementById('descrizioneDaCercare') as HTMLInputElement).value;

    if(this.query.eliminaCategoria(desc2)){
      this.categoriaSub = this.query.getCategoriaListener()
      .subscribe((categoria: Categoria[]) => {
        this.categoria = categoria;
    });
    }
    alert("Categoria eliminata con successo")
    window.location.reload();
    return true
  }

}
