import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { InserimentoqueryService } from '../inserimentoquery.service';

@Component({
  selector: 'app-reportnoleggi',
  templateUrl: './reportnoleggi.component.html',
  styleUrls: ['./reportnoleggi.component.css']
})
export class ReportnoleggiComponent implements OnInit {

  categoria: any= [];
  private categoriaSub: Subscription;
  constructor(
    private query : InserimentoqueryService,
    private http: HttpClient
    
    ) { }

  ngOnInit(): void {
    this.start();
  }

  ngOnDestroy() {
    this.start();
  }

  start(){
    this.http.get<{message: string, categoria: any}>('http://localhost:3000/api/storico/noleggi').subscribe(
        (categoriaData)=>{
            this.categoria = categoriaData.categoria;
            return this.categoria
        });
  }
}
