import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Categoria } from   "../../models/categoria.model";



@Injectable({
  providedIn: 'root'
})
export class InserimentoqueryService {
  private categoria: Categoria[] = [];
  private categoriaUpdate= new Subject<Categoria[]>();
  constructor(private http: HttpClient) { }

  addCliente(id,nominativo,patente,cc){
      const clienteDaAggiungere = {id:id,nominativo:nominativo, patente:patente, cc: cc};
      this.http
      .post<{message: string, risposta: any}>('http://localhost:3000/api/cliente', clienteDaAggiungere)
      .subscribe(
        (responseData)=>{
          console.log(responseData.message, responseData.risposta);
        }
      )
      /*this.categoria.push(clienteDaAggiungere);
      this.categoriaUpdate.next([...this.categoria]);
      */
      return true;
    }

  noleggio(id,box,data_inizio,data_fine){
      const noloDaAggiungere = {id:id,box:box, data_inizio:data_inizio, data_fine: data_fine};
      this.http
      .post<{message: string, risposta: any}>('http://localhost:3000/api/nolo', noloDaAggiungere)
      .subscribe(
        (responseData)=>{
          console.log(responseData.message, responseData.risposta);
        }
      )
      /*this.categoria.push(clienteDaAggiungere);
      this.categoriaUpdate.next([...this.categoria]);
      */
      return true;
    }
    
  getCategoria(){
    return this.http.get<{message: string, categoria: Categoria[]}>('http://localhost:3000/api/categoria').subscribe(
      (categoriaData)=>{
          this.categoria = categoriaData.categoria;
          this.categoriaUpdate.next([...this.categoria]);
      });
  }

  getCategoriaListener() {
    return this.categoriaUpdate.asObservable();
  }

  /*getAutoByTarga(targa:string){
    const categoriaDaCercare = {targa : targa};
    this.http
    .post<{message: string, risposta: any}>('http://localhost:3000/api/categoria/id_auto', categoriaDaCercare)
    .subscribe(
      (responseData)=>{
        console.log(responseData.message);
        return responseData.risposta;
      }
    )
  } */

  cercaTarga(targa:string){
    const categoriaDaCercare = {targa : targa};
    let box;
    this.http
    .post<{message: string, risposta: any}>('http://localhost:3000/api/categoria/preventivo', categoriaDaCercare)
    .subscribe(
      (responseData)=>{
        console.log(responseData.message);
        box=responseData.risposta;
        this.categoria.push(box);
        this.categoriaUpdate.next([...this.categoria]);
      }
    )
    return true;
  }

  addCategoria(id:any, descrizione:string, prezzo_giornaliero:any, 
    prezzo_settimanale:any,prezzo_mensile:any ){
      const categoriaDaAggiungere: Categoria= { id:id, descrizione:descrizione, prezzo_giornaliero:prezzo_giornaliero, 
        prezzo_settimanale:prezzo_settimanale,prezzo_mensile:prezzo_mensile};
      this.http
      .post<{message: string, risposta: any}>('http://localhost:3000/api/categoria', categoriaDaAggiungere)
      .subscribe(
        (responseData)=>{
          console.log(responseData.message, responseData.risposta);
        }
      )
      this.categoria.push(categoriaDaAggiungere);
      this.categoriaUpdate.next([...this.categoria]);

      return true;
    }

    cercaCategoria(descrizione:string){
        const categoriaDaCercare = {descrizione : descrizione};
        let box;
        this.http
        .post<{message: string, risposta: any}>('http://localhost:3000/api/categoria/ricerca', categoriaDaCercare)
        .subscribe(
          (responseData)=>{
            console.log(responseData.message);
            box=responseData.risposta;
            this.categoria.push(box);
            this.categoriaUpdate.next([...this.categoria]);
          }
        )
  
        return this.categoria;
      }

      modificaCategoria( descrizione:string, prezzo_giornaliero:any, 
        prezzo_settimanale:any,prezzo_mensile:any, descmod:string){
          const categoriaDaModificare = {descrizione:descrizione, prezzo_giornaliero:prezzo_giornaliero, 
            prezzo_settimanale:prezzo_settimanale,prezzo_mensile:prezzo_mensile, descmod:descmod};
          this.http
          .post<{message: string, risposta: any}>('http://localhost:3000/api/modifica', categoriaDaModificare)
          .subscribe(
            (responseData)=>{
              console.log(responseData.message, responseData.risposta);
            }
          )
          return true;
        }

        cercaAuto(descrizione:string){
          const categoriaDaCercare = {descrizione : descrizione};
          let box;
          this.http
          .post<{message: string, risposta: any}>('http://localhost:3000/api/categoria/parco', categoriaDaCercare)
          .subscribe(
            (responseData)=>{
              console.log(responseData.message);
              box=responseData.risposta;
              this.categoria.push(box);
              this.categoriaUpdate.next([...this.categoria]);
            }
          )
          return true;
        }

        eliminaCategoria(descrizione:string){
          const categoriaDaCercare = {descrizione : descrizione};
          let box;
          this.http
          .post<{message: string, risposta: any}>('http://localhost:3000/api/elimina', categoriaDaCercare)
          .subscribe(
            (responseData)=>{
              console.log(responseData.message);
              box=responseData.risposta;
              this.categoria.push(box);
              this.categoriaUpdate.next([...this.categoria]);
            }
          )
    
          return this.categoria;
        }

  
  
}
